*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .heading_style{
    padding: 30px 0px;
    text-align: center;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-transform: uppercase;
    font-size: 23px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #757373;
    background-color: #fff;
    font-size: 1.5rem;
    margin-bottom: 30px;
  }
  
  .cards{
    width: 100%;
    height: auto;
  }
  
  .card{
    /* margin: 105px; */
    margin: 6%;
    transition: all 0.4s cubic-beizer(0.175,0.885,0,1);
    background-color: #fff;
    width: 21%;
    border-radius: 15px;
    box-shadow: 0px 13px 10px -7px rgba(0, 0, 0,0.1);
    float: left;
  }
  
  .card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0,0.1);
    transform: scale(1.05,1.05);
  }
  
  .card_img{
    width: 100%;
    height: 235px;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;  
  }
  
  .card_info{
    background-color: #fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px; 
    padding: 16px 24px 24px 24px;  
  }
  
  .card_category{
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
  }
  .card_title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #fff;
    text-transform: capitalize;
  }
  
  button{
    padding: 5px 8px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
  }
  
  body {
    margin: 0;
    padding: 0;
    background-color: #aabeb9;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
  }
  
  @media (max-width: 768px){
    .card{
      float:none;
      margin: 5%;
      width: 90%;
    }
  }
  
  